				sequence: '',
				file: null
			},
			fileName: '',
			top_sequences: [
				{
					sequence: 'GTGACATTTTGAATTTGAAGTATTATAATGTGTGG',
					strength: '253.547558',
					score: '100'
				}
			],
			isSubmit: false
		}
	},
	methods: {
		onsubmit() {
			const data = new FormData()
			data.append('project_name', this.form.project_name)
			data.append('sequence', this.form.sequence)
			data.append('file', this.form.file)
			axios.post('http://139.129.26.28:81/submit', data).then(() => {
				this.isSubmit = true
			})
		},
		downloadResult(file) {
			if (!this.isSubmit) {
				alert('请先提交')
				return false
			}
			// window.open(`http://139.129.26.28:80/download/${this.fileName}`)
			const link = document.createElement('a')
			link.href = `http://139.129.26.28:81/download/${file}`
			link.download = this.fileName
			link.click()
		},
		uploadFile(event) {
			console.log(event.target.files)
			this.form.file = event.target.files[0]
			console.log(this.form)
		},
		toPage() {
			console.log('toPage')
			window.open('http://139.129.26.28:80/')
		}
	}
}
</script>

